.bento-box {
    display:grid;
    flex-direction: column;
    grid-gap: 10px;
    margin-right: 25px;
  }

  .information{
    display: flex;
    grid-gap: 10px;
    justify-content: space-between;
    margin: 20px 0 0;
  }

  .side-bento{
    display: grid;
    flex-direction: column; 
    height: 492px;
    align-content: space-between;
    justify-content: space-between;
  }

  .bento-container1, .bento-container2, .bento-container3{
    justify-content: center;
    overflow: hidden;
    //border: 1px solid var(--font-color);
    border-radius: 5px;
  }
  
  .bento-container1, .bento-container2{
    display: flex;
    align-content:center;
  }

  .bento-container1{
    width: 600px;
    height: 492px;
  }
  .bento-container2{
    width: 290px;
    height: 241px;
  }
  
  .bento-container3{
    display: grid;
    width: 900px;
    height: 241px;
  }

  @media (max-width: 1600px){
    .information{
      flex-direction: column;
      max-width: 950px;
      padding-right: 50px ;
      border-right: 1px solid var(--font-color);

    }
  }