:root {
    --red: #CF3F3A;
  }

h1, h2, h3, h4, h6, ul, li, button{
    color: var(--font-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: none;
    font-style: normal;
    border: none;
    background-color: transparent;
    margin: 0;
    transition: 0.3s ease-in-out;
    list-style: none;
}

p{
    color: var(--font-color);
    letter-spacing: 1px;
    margin: 0;
    transition: 0.3s ease-in-out;
    font-family: 'Tusker Grotesk', sans-serif;
}

h1{
    font-size: 2em;
    margin-bottom:15px;
} 

h3{
    margin-top: 20px;
}

h4{
    font-size: 1em;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--font-color);
    color: var(--tertiary-color);
}

h6{
    font-size: 0.7em;
    margin: 0px 0px 10px 0px;
}

ul{
    padding-left: 0px;
}

.intro-h4{
    border-bottom: 0px;
}

button{
    color: var(--font-color);
    background: transparent;
    border: none;
    font-weight: 600;
}

header{
    margin-bottom: 70px;
}

.header-container{
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 0;
    width: 100%;
    z-index: 999;
}

.header{
    position: relative;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--font-color);
    backdrop-filter: blur(5px);
    padding: 15px 40px 10px 0px;
    h1 {
        margin-bottom: 2px;
    }
    h4{
        border-bottom: 0px;
        padding: 0px;
    }
} 

.progress-bar {
    position: relative;
    height: 2px;
    background-color: var(--font-color); /* Adjust as needed */
  }

.scrolled{
    h1 {
        font-size: 16px;
    }
    h4{
        font-size: 12px;
        color: #CF3F3A;
    }
}

.nav-buttons-container{
    position: relative;
    display: flex;
    align-items: end;
}

.nav-buttons{
    position: relative;
    margin: 0px 10px 0px 0px;
    padding-left: 0px;
}

.homePage-title{
    text-shadow: 12px 12px 7px rgba(0, 0, 0, 0.7);
}

.homePage-h6{
    margin: 0;
    text-shadow: 12px 12px 7px rgba(0, 0, 0, 0.7);
    transform: translateY(7vw);
    text-align: center;
}

.homePage-h3{
    margin: 0;
    font-size: 20vw;
    text-shadow: 12px 12px 7px rgba(0, 0, 0, 0.7);
}

.homePage-display{
    transform: translate(80%, 50%); 
    position: absolute;
    display: flex;
    height: 500px;
    width: 45vw;
    justify-content: center;
    align-items: center;
    overflow: visible;
    transition: 0.3s ease-in-out;
}

.homePage-button{
    position: relative;
    display: flex;
    border: 1px solid var(--font-color);
    margin: 0;
    padding: 20px;
    align-items: center;
    justify-content: center;
}

.homePage-text{
    font-size: 16px;
    margin: 0;
    cursor: pointer; 
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px); 
        color: var(--red); 
    }
}

#react-unity-webgl-canvas-2{
    aspect-ratio: 16 / 9;
}



































.buttonCC,.buttonC, .nav-buttons{
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
    ul{
        margin-right: 0;
    }
}

.buttonC{
    margin-right: 40px;
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: #F4F6F6; }
.boxB {background-color: #1A1E23; }

.homepage-movement{
    position: relative;
    display: grid;
    justify-items: center;
    h3{
        justify-self: center;
        margin: 20px;
    }
    h5{
        font-size: 13px;
    }
}

.icon-button-switch{
    position: relative;
    font-size: 16px;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
}

.icon-button-switch-container{
    position: relative;
    display: grid;
    margin: 20px;
    padding: 20px;
    border: 1px solid var(--font-color);
    
}

.icon-container-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    letter-spacing: 2px;
}

.icon-container{
    position: relative;
    display: flex;
    align-items: center;
    justify-items: space-around;
    background-color: var(--secondary-color);
    flex-basis: calc(20%); /* 20% for 5 items per line, minus margins */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add shadow */
    border-radius: 10px;
    padding: 0px 10px 0 10px;
    margin: 20px;
    padding: 10px;
    min-width: 250px;
    cursor:pointer;
    overflow: hidden;
    &:hover{
        transform: translateY(-5px);
        //background-color: var(--red);
        .home-noPro {transform: translateX(-50%);}
        .home-htPro {transform: translateX(-33%);} 
        .home-csPro {transform: translateX(-28%);}
        .home-jsPro {transform: translateX(-43%);}
        .home-jaPro {transform: translateX(-47%);}
        .home-sqPro {transform: translateX(-25%);}
        .home-pyPro {transform: translateX(-47%);}
        .home-cPro  {transform: translateX(-34%);}
        .home-saPro {transform: translateX(-28%);}
        .home-unPro {transform: translateX(-57%);}
        .home-conPro{transform: translateX(-14%);}
    }
    
    .home-conPro{transition: 0s;}
}

.icons{
    height: 50px;
    width: 50px;
    margin-right: 10px;
}

.home-noPro, .home-htPro, .home-csPro, .home-jsPro, .home-jaPro,.home-sqPro,.home-pyPro,.home-cPro,.home-saPro,.home-unPro, .home-conPro{
    position: absolute;
    background-color: var(--tertiary-color);
    height: 100%;
    width: 120%;
    transform: translateX(-105%);   
    transition: 0.3s ease-in-out;    
    z-index: -1; 
}

.page, .page1{
    position: relative;
    width: 100%;
}

.page1{
    margin-top:50px;
    height: calc(100vh - 100px); 
    display:flex;
    flex-direction: column;
    border-bottom: 3px solid var(--font-color);
    width: 100%;
    overflow: hidden;
}

.colourPanel{
    position: fixed;
    display:flex;
    bottom: 0;
    margin-bottom: 15px;
    backdrop-filter: blur(3px);
}

.order{
    position: relative;
    display: flex;
    //height: calc(100% - (3rem + 65px));
}

.links{
    margin-top: 30px;
    position: sticky;
    display:grid;
    justify-content: start;
    width: 120px;
    border-right: 1px solid var(--font-color);
    height: 185px;
    top: 50px;
    transition: 0.3s ease-in-out;

}

.links.scrolled{
    position: fixed;
    display: inline;
    top: 0px;
    right: 0px;
    height: 65px;
    margin: 0;
    width: auto;
    border: 0;
    backdrop-filter: blur(0px);
}

.page-display{
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin-left: 20px;
    width: calc(100% - 120px);
}

.display{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 500px;
    width: 45vw;
    overflow: visible;
}

.projectDiv{
    position: relative;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.projectUL{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: top;
    justify-content: space-around;
    overflow: hidden;
    h2{
        margin-top: 20px;
    }
}

.projectULL {
    height: 500px;
    position: sticky;
    top: 80px;   
}

.blur-overflow-container{
position: relative;
display: flex;
height: 500px;
overflow: hidden;
width: 98%;//---------------------------------
justify-content: center;
align-items: center;
z-index: 5;
}

.projectList{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 550px;
    overflow-y: scroll;
    padding: 50px 30px 70px 0px;
    &::-webkit-scrollbar {
        display: none; 
    }
}

.blur-container-bottom, .blur-container-top{
    position: absolute;
    width: 100%;
    height: 40px;
    backdrop-filter: blur(4px);
}

.blur-container-bottom {bottom: 0;}

.blur-container-top {top: 0;}

.listTags{
    display: flex;
    align-items: baseline;
    
    h3{
        font-size: 4em;
        font-weight: 100;
    }
    h6{
        font-size: 0.5em;
    }
    &:hover {
        
        h6{color: #CF3F3A;
            transform: translateY(-5em);}
        h3{color:#a6adb4;
            transform: translateY(-3px);}
    }
}

.previewBox {
    position: absolute;
    display: grid;
    top: 0;
    right: 0;
    margin-top: 10px;
    padding: 10px;
    width: 35vw;
    height: 500px;
    justify-content: center;
    align-content: center;
  }

.titles {
    text-align: center;
    padding: 0; /* Add this line to override the default margin */
}

.site-video, .pitc-img{
    width: 700px;
}

.pitc-button{
    padding: 1px;
    :hover{
        cursor: pointer;
    }
}

.close-icon{
    position: fixed;
    top: 30px;
    right: 30px;
    height: 30px;
    width: 30px;
    
}

.infectionSim{
    height: 800px;
    width: 800px;
}

.showcase {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.9);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(4px);
  }

  .text-container{
    position: relative;
    display: flex;
    justify-content: center;
  }

  .text{
    position: relative;
    width: 40%;    
  }


  

@media (max-width: 1620px) {

}

@media (max-width: 1490px) {
    .blur-overflow-container{
        justify-content: start;
        align-items: center;
        }

    .projectUL{
        margin-top: 50px;
    }

    .projectULL {
        height: 500px;
        position: relative;
    }

    .projectDiv{
        flex-direction: column;
    }
}
@media (max-width: 1405px) {
.icon-container{
    min-width: 0;
    }
}

@media (max-width: 1295px) {
    .projectUL{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
    }

    .display, .site-video,.pitc-img{
        width: 80vw;
        aspect-ratio: 16 / 9;
        max-width: 700px;
        
    }

    #react-unity-webgl-canvas-2{
        width: 80vw;
        aspect-ratio: 2 / 1;
        max-width: 700px;
    }

}

@media (max-width: 1150px) {
    .icon-container-container{
        justify-content: left;
        transition: 0.3s ease-in-out;
    }

    .icon-container{
        position: relative;
        display: flex;
        align-items: center;
        justify-items: space-around;
        background-color: var(--secondary-color);
        flex-basis: calc(15%); /* 20% for 5 items per line, minus margins */
        border-radius: 2px;        
        margin: 0px;
        padding: 10px;
        min-width: 180px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0);
        border: 1px solid var(--font-color);
    }
}

@media (max-width: 1115px){
    .homePage-display{
        transform: translate(60%, 50%); 
    }
}

@media (max-width: 1000px){
    .homePage-display{
        transform: translate(50%, 50%); 
    }

    .text{
        position: relative;
        width: 100%;    
      }
    
}

@media (max-width: 885px){
    
    .header{
        flex-direction: column;
        padding-bottom: 0px;
        .nav-buttons{font-size: 12px;}
        .nav-buttons-container{
            width: 100%;
            margin-top: 10px;
            justify-content: center;
        }
    }
    
    .homePage-display{
        transform: translate(50%,20%); 
    } 
}

@media (max-width: 685px){
    .homePage-h6{
        white-space:initial;
    }
}

@media (max-width: 600px){
    h1, h2, h3, h4, ul, li, button{
        white-space:inherit;
    }    

    .page-display{
        padding: 0px;
        margin-left: 0px;
        width: calc(100% - 120px);
    }

    .listTags{
        flex-direction: column;
    }

    .order{
        flex-direction: column;
        .links{
            position: relative;
            display:flex;
            height: auto;
            width: 100%;
            top: 0px;
            font-size: x-small;
            justify-content: space-around;
            border-bottom: 1px solid var(--font-color);
            border-right: 0px;
            margin-bottom: 20px;
            padding-bottom: 20px;
        }
    }

    .homepage-movement{
        justify-items: start;
        h3{
            justify-self: start;
        }
    }

    .titles{
        text-align:start;
        margin: 20px;
    }   
}

@media (max-width: 500px){
    .icon-button-switch-container{
        width: auto;
        min-width: 250px;
    }
}

@media (max-width: 480px){
    header{
        margin-bottom: 0;
    }

    body{
        margin: 0;
        padding: 0px 10px 0px;
    }

    .page1{
        height: calc(100vh - 47px); 
    }
        
    .icon-container-container{
        justify-content: start;
    }

    .menuButton{
        position: fixed;
        display:grid;
        top: 20px;
        right: 20px;
        z-index: 2;
        backdrop-filter: blur(5px);
        text-shadow: 2px 2px 4px var(--background-color, 0.7);
        &:hover{
            transform: translateY(-3px); 
            color: var(--red); 
            cursor: pointer;
        }
    }
    
    .menu {
        width: 75%; 
        height: 100%; 
        background-color: var(--background);
        position: fixed;
        display: flex;
        right: -120%; 
        top: 0;
        transition: 0.5s ease;
        z-index: 2; 
        border-left: 1px solid var(--font-color);
    }

    .menu.open {
        right: 0;
        transition: 0.5s ease;
    }

    .innerButton{
        position: absolute;
        top: 20px;
        right: 20px;
        z-index: 2; 
        &:hover{
            cursor: pointer;
            transform: translateY(-3px); 
            color: var(--red); 
        }
    }

    .blurBackground{
        width: 100%; 
        height: 100%; 
        backdrop-filter: blur(4px);
        position: fixed;
        top: 0; 
        left: 0;
        z-index: -1;
        &.open{
            z-index: 1;
        }
      }

    .nav-buttons-container{
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        text-align: end;
        align-items: end;
        justify-content: end;
        margin-bottom: 50px;
    }
    
    .nav-buttons{
        position: relative;
        justify-content: center;
        margin: 0px 0px 20px;
        padding-left: 0px;
    }

    .colourPanel{
        position: absolute;
        display: grid;
        margin: 15px;
    }
    
}

@media (max-width: 400px){
    
}



@media (max-width: 320px){
    body{
        padding: 0;
        height: 100vh;
        overflow: hidden;
        background-image: url('../images/gon.jpg');
        background-size: cover;
        background-position: center;
    }

    .small-canvas{
        display: grid;
        height: 100%;
        width: 100%;
        align-items: center;
        h2{
            color: var(--red);
            padding: 5px;
            backdrop-filter: blur(4px);
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
        }
    }
}
